* {
  box-sizing: border-box;
  margin: 0;
}

.App-wrapper {
  width: 100vw;
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(auto-fit, 300px);
  grid-gap: 10px;
  grid-auto-rows: 100vh;
  background-color: #222;
  justify-content: space-evenly;
  justify-items: center;
  align-items: center;
}
